<template>
  <Modal 
    	v-model="showAdd"
    	footer-hide
    	scrollable 
    	onVisibleChange="showAdd"
    	title=""
    	width="800"
    >
    	<Input v-model="findStr">
	        <Select v-model="findType" slot="prepend" style="width: 80px">
	            <Option value="day"></Option>
	            <Option value="month"></Option>
	        </Select>
	        <Button type="primary" @click="findFriend" slot="append" icon="ios-search"></Button>
	    </Input>
    	
    	<template v-for="userInfo in findUserInfo">
		    <Card style="width:150px">
		        <p slot="title">
		            <Icon type="md-person" />
		            {{userInfo.username}}
		        </p>
		        <Poptip slot="extra" placement="right" width="400">
		            <a><Icon type="md-add-circle" :size="20"/></a>
		            <div slot="content" >
		            	<Input v-model="friendMsg" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="" /><br/>
		            	<Button @click="sendFriendMsg(userInfo.userId)" style="float:right;"></Button>
		            </div>
		        </Poptip>
		        <ul>
		            <li>
		                <a  target="_blank">{{ userInfo.username }}</a>
		            </li>
		        </ul>
		    </Card>
		</template>
    </Modal>
</template>

<script>
import '../assets/js/jquery1.9.1/jquery.js'
import axios from 'axios'
import qs from 'qs'
export default {
  name: 'AddFriend',
  data () {
    return {
        showAdd:false,			//
	    findStr:'',				//Input
	    findType:'',			//
	    findUserInfo:[],		//
	    friendMsg : ''			//
    }
  },
  props : ['loginUserId','showAddFriend']
  ,
  watch : {
  		showAddFriend (val){
  			this.showAdd = val;
  		}
  },
  methods : {
  		//
    	sendFriendMsg (userId) {
    		var msg = {};
    		msg.from = this.loginUserId;
    		msg.to = userId;
    		msg.content = this.friendMsg;
    		msg.type = 5;
    		
    		this.$emit("sendmsg",msg);
    	},
  	 	//
    	findFriend () {
    		var self = this;
	  		axios.get('/friend/find?findStr='+this.findStr)
	  		.then(function(response){
	  			self.findUserInfo = response.data;
	  			self.$forceUpdate();
	  		})
    	},
    	//10
		friendManager (type,msg) {
		  	  var self = this;
	  		  if(type == 1){ 
	  		    axios.post('/friend/add',qs.stringify({
		  			userIdA : msg.to[0],
		  			userIdB : msg.from,
		  		}))
		  		.then(function(response){
		  			//
		  			var msg_send = {};
		    		msg_send.from = msg.to[0];
		    		msg_send.to = msg.from;
		    		msg_send.content = '';
		    		msg_send.type = -1;
		    		
		    		self.$emit("sendmsg",msg_send);
		    		//
		    		self.showQuanJuMessage(msg);
		  			
		  		}) 
	  		  }
	  		  if(type == 0){
	  		  		var msg_send = {};
		    		msg_send.from = msg.to[0];
		    		msg_send.to = msg.from;
		    		msg_send.content = '' ;
		    		msg_send.type = -1;
		    		
		    		self.$emit("sendmsg",msg_send);
	  		  }
		  },
		  
		  showQuanJuMessage (msg) {
			  	this.$Message.success({
		    		duration: 5,
	                closable: true,
	                render: h => {
	                    return h('span', [
	                        msg.content,
	                        h('a', 'render')
	                    ])
	                }
	            });
		  }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	
</style>



